* {
  box-sizing: border-box;
}

@font-face {
  font-family: Avenir;
  src: local(Avenir), url("Avenir.8bcf8c72.ttc"), url("Avenir-Book.657c3568.ttf"), url("Avenir-Heavy.01d9ab74.ttf");
  font-size: 300px;
}

form {
  width: 100%;
  margin-top: 20px;
}

.big-search {
  background: #fff;
  border: 1px solid #e1e7ed;
  border-radius: 3px;
  align-items: center;
  width: 100%;
  height: 70px;
  display: flex;
  box-shadow: 0 1px 8px #3d4e611a;
}

.big-search:hover {
  box-shadow: 0 4px 4px #00000040;
}

.search-img {
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  position: absolute;
}

.search-img.big {
  width: 19px;
  height: 19px;
  margin-left: 26px;
}

.big-search input {
  border: none;
  align-items: center;
  width: 100%;
  height: 100%;
  padding-left: 62px;
  font-size: 16px;
  display: flex;
}

.small-search {
  background: #fff;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  align-items: center;
  width: 100%;
  height: 42px;
  display: flex;
  box-shadow: 0 1px 8px #3d4e611a;
}

.small-search:hover {
  box-shadow: 0 4px 4px #00000040;
}

.search-img.small {
  background-size: contain;
  width: 11px;
  height: 11px;
  margin-left: 13px;
}

.small-search input {
  border: none;
  width: 100%;
  height: 100%;
  margin-left: 9px;
  padding-left: 33px;
  font-size: 14px;
}

.small-search input::placeholder {
  color: #3d4e61;
  font-size: 14px;
  font-style: normal;
  font-weight: 350;
  line-height: normal;
}

.big-search input::placeholder {
  color: #3d4e61;
  font-family: Avenir, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  display: flex;
}

input:focus {
  color: #3d4e61;
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
  border: 1px solid #e1e7ed;
  border-radius: 3px;
  outline: none;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
}

